*{
    margin:0;
    padding:0;
  }
.main-technical{
    border:0px solid blueviolet;
    margin-top: 8px;
    width:100%;
    
}
.main-technicals{
    border:0px solid blueviolet;
    margin-top: 8px;
    width:100%;
    position:relative;
    animation-name:mymove;
    animation-duration: 1s;
}
@keyframes mymove{
    from {right:0px;}
    to {left:-120px;}
}
.skill-outer{
       display: flex; 
       border:0px solid yellow;
       justify-content: center;
       margin: auto;
 }

@media(min-width:821px){

    .frontend-heading{
        margin-bottom: 50px;
    }
    .skill-outer{
        padding:20px;
    }

    .TechSkills{
        width:90%;
        height: 280px;
        padding: 20px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
       border:0px solid red;
    }
    .front-end-skills{
        border:0px solid blue;
        display:flex;
        align-items: center;
        padding:10px 10px 0px 10px;
    }
    .front-end-skills>div{
        border:0px solid green;
        width:100%;
        height:170px;
    }
    .TechSkills img{
        width:100%;
        height: 100%;
    }
    .backend-outer{
        display: flex;
        justify-content: center;
        border:0px solid yellow;
        height: 280px;
        padding:20px;
    }
    .backend{
        width:90%;
        height:100%;
        
        padding:40px 20px 40px 20px;
        
        border:0px solid orangered;
        display: flex;
        justify-content: space-around;
        gap:10px;
        box-shadow:rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }
    
    .backend img{
        width:100%;
        height:70%;
    }
    .backend-heading{
        margin-bottom: 50px;
        margin-top: 50px;
    }
    
}


@media(min-width:600px) and (max-width:820px){
    .frontend-heading{
        margin-bottom: 40px;
    }
    .skill-outer{
        padding:20px;
        
    }
    
    .TechSkills{
        width:100%;
        border:0px solid green;
        padding:40px 0px 40px 0px;
        margin-bottom: 40px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }
    /* style={{ display: "grid", gridTemplateColumns:"repeat(6,1fr)",gridTemplateRows:"160px", marginTop: "20px" }} */
    .front-end-skills{
        width:100%;
        border:0px solid red;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap:20px;
    }
    .front-end-skills>div{
        border:0px solid blue;

    }
    .TechSkills img{
        width: 100px;
        height: 100px;
        margin:auto;
    }
    .backend-outer{
        width:100%;
        border:0px solid green;
        padding: 20px;
    }
    .backend{
        border:0px solid red;
        width:100%;
        padding:40px 0px 40px 0px;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        justify-content: space-around;
        gap:20px;
        height: auto;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }
    .backend>div{
        border:0px solid blue;
    }
    .backend img{
        width:100px;
       height:100px;
       margin:auto;
    }
}


@media(max-width:599px){

    .frontend-heading,.backend-heading{
        margin-bottom: 20px;
    }
    
    .skill-outer{
        padding:20px;
    }

    .TechSkills{
        width:100%;
        border:0px solid green;
        padding:20px 0px 20px 0px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        margin-bottom: 20px;
    }
    /* style={{ display: "grid", gridTemplateColumns:"repeat(6,1fr)",gridTemplateRows:"160px", marginTop: "20px" }} */
    .front-end-skills{
        width:100%;
        border:0px solid red;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        
    }
    .front-end-skills>div{
        border:0px solid blue;

    }
    .TechSkills img{
        width: 70px;
        height: 70px;
        margin:auto;
    }
    .backend-outer{
        width:100%;
        border:0px solid green;
        padding: 20px;

    }
    .backend{
        border:0px solid red;
        width:100%;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        padding:20px 0px 20px 0px;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap:20px;
    }
    .backend>div{
        border:0px solid blue;
    }
    .backend img{
        width:70px;
       height:70px;
       margin:auto;
    }
}
